import React, {useEffect} from 'react';
import style from "./App.module.scss";
import {Header} from "../A1_Header/Header";
import {CustomAlert} from "../X_common/CustomAlert/CustomAlert";
import {observer} from "mobx-react-lite";
import {useStore} from "../../store/useStore";
import {chainId, getProvider, getShopContract} from "../../helpers/ethers.helper";
import {Navigate, Route, Routes} from 'react-router-dom';
import {getRoutes} from "../A1_Header/routes";


export const App = observer(() => {
    const {
        cryptoStore: {
            currentAccountAddress, errorHandler,
            getShopOwner, shopOwner,
            getPricesBuy, getPricesSell,
            getShopBalance, getShopTokensBalance,
            // getTokenPriceForSell, getTokenPriceForBuy,
            // getShopTokenBalance,
        }
    } = useStore();

    // при изменении состояния смарт-контракта одним акккаунтом, другие обрабатывают это изменение через события
    // PriceBuy: getPricesBuy - обновить цену покупки
    // PriceSell: getPricesSell - обновить цену продажи
    // Buy, BuyBatch, Sell, SellBatch, Mint, MintBatch, Burn, BurnBatch: getShopTokensBalance - обновить баланс токенов магазина
    // Buy, BuyBatch, Sell, SellBatch: setShopBalance - обновить баланс эфира магазина

    //========= ADD EVENT LISTENERS =========
    const addListener = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getShopContract(provider);
                const startBlockNumber = await provider.getBlockNumber();

                provider.on("error", (e) => console.log(e));

                // PriceBuy
                // @ts-ignore
                contract.on("PriceBuy", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getPricesBuy();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                });

                // PriceSell
                // @ts-ignore
                contract.on("PriceSell", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getPricesSell();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                });

                // Buy
                // @ts-ignore
                contract.on("Buy", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getShopTokensBalance();
                            await getShopBalance();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                });

                // BuyBatch
                // @ts-ignore
                contract.on("BuyBatch", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getShopTokensBalance();
                            await getShopBalance();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                });

                // Sell
                // @ts-ignore
                contract.on("Sell", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getShopTokensBalance();
                            await getShopBalance();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                });

                // SellBatch
                // @ts-ignore
                contract.on("SellBatch", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getShopTokensBalance();
                            await getShopBalance();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                });

                // Mint
                // @ts-ignore
                contract.on("Mint", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getShopTokensBalance();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                });

                // MintBatch
                // @ts-ignore
                contract.on("MintBatch", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getShopTokensBalance();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                });

                // Burn
                // @ts-ignore
                contract.on("Burn", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getShopTokensBalance();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                });

                // BurnBatch
                // @ts-ignore
                contract.on("BurnBatch", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getShopTokensBalance();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                });

            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= МОНТИРОВАНИЕ =========//
    useEffect(() => {
        const omMountHandler = async () => {
            if (window.ethereum) {
                const provider = getProvider();
                const network = await provider.getNetwork();

                // проверка совпадения сети в которой развернут смарт-контракт с той, к которой подключились
                if (network.chainId === chainId) {
                    await addListener();
                    await getShopOwner();
                }
            }
        }
        omMountHandler().then();
    }, [window.ethereum]);

    return (
        <div className={style.app}>
            <Header/>
            <CustomAlert/>
            <main className={style.main}>
                <Routes>
                    {
                        getRoutes({currentAccountAddress, shopOwner})
                            .map(({path, element, condition}, key) => (
                                <Route key={key}
                                       path={path}
                                       element={condition ? element : <Navigate to="/" replace={true}/>}
                                />
                            ))
                    }
                </Routes>
            </main>
        </div>
    );
})

