import React, {useEffect, useState} from "react";
import style from "./Admin.module.scss";
import {observer} from "mobx-react-lite";
import {useStore} from "../../store/useStore";
import {ethers} from "ethers";
import {useNavigate} from "react-router-dom";
import {chainId, getProvider, getShopContract, shopAddress} from "../../helpers/ethers.helper";
import {LinearProgress, Typography} from "@mui/material";
import Button from "@mui/material/Button";
import CircularProgress from "@mui/material/CircularProgress";
import {Balances} from "../B0_Shop/Balances/Balances";
import {PriceForm} from "./PriceForm/PriceForm";
import {items} from "../../assets/jpeg/items";
import {BatchForm} from "../X_common/BatchForm/BatchForm";

export const Admin = observer(() => {
    const {
        cryptoStore: {
            loading, currentAccountAddress,
            shopOwner, errorHandler,
            shopBalance, getShopBalance,
            shopTokensBalance, getShopTokensBalance,
            pricesBuy, pricesSell,
            getPricesBuy, getPricesSell,
        }
    } = useStore();

    const navigate = useNavigate();
    useEffect(() => {
        if (
            !(currentAccountAddress && shopOwner && ethers.utils.getAddress(currentAccountAddress) === ethers.utils.getAddress(shopOwner))
        ) {
            navigate(-1);
        }
    }, []);

    //========= МОНТИРОВАНИЕ =========//
    useEffect(() => {
        const omMountHandler = async () => {
            if (window.ethereum) {
                const provider = getProvider();
                const network = await provider.getNetwork();

                // проверка совпадения сети в которой развернут смарт-контракт с той, к которой подключились
                if (network.chainId === chainId && currentAccountAddress) {
                    await getShopBalance();
                    await getShopTokensBalance();
                    await getPricesBuy();
                    await getPricesSell();
                }
            }
        }
        omMountHandler().then();
    }, [window.ethereum, currentAccountAddress]);

    const [localLoading, setLocalLoading] = useState(false);

    // CHANGE PRICE BUY
    const onChangePriceBuy = (tokenId: number) => async (price: number) => {
        try {
            if (currentAccountAddress) {
                const provider = getProvider();
                const shopContract = getShopContract(provider);
                const signer = provider.getSigner(currentAccountAddress);
                const tx = await shopContract
                    .connect(signer)
                    .setPriceBuy(tokenId, price);
                await tx.wait(); // ждем ее завершения
                await getPricesBuy(); // получить новую цену

            }
        } catch (e: any) {
            errorHandler(e);
        }
    }

    // CHANGE PRICE SELL
    const onChangePriceSell = (tokenId: number) => async (price: number) => {
        try {
            if (currentAccountAddress) {
                const provider = getProvider();
                const shopContract = getShopContract(provider);
                const signer = provider.getSigner(currentAccountAddress);
                const tx = await shopContract
                    .connect(signer)
                    .setPriceSell(tokenId, price);
                await tx.wait(); // ждем ее завершения
                await getPricesBuy(); // получить новую цену

            }
        } catch (e: any) {
            errorHandler(e);
        }
    }

    // MINT
    const mint = async (ids: number[], amounts: number[]) => {
        try {
            if (window.ethereum && currentAccountAddress) {
                const provider = getProvider();
                const shopContract = getShopContract(provider);
                const signer = provider.getSigner(currentAccountAddress);
                if (ids.length > 1) {
                    const tx = await shopContract
                        .connect(signer)
                        .mintBatchToShop(ids, amounts);
                    await tx.wait(); // ждем ее завершения
                }
                if (ids.length === 1) {
                    const tx = await shopContract
                        .connect(signer)
                        .mintToShop(ids[0], amounts[0]);
                    await tx.wait(); // ждем ее завершения
                }
                await getShopTokensBalance();
            }
        } catch (e) {
            errorHandler(e);
        }
    }

    // BURN
    const burn = async (ids: number[], amounts: number[]) => {
        try {
            if (window.ethereum && currentAccountAddress) {
                const provider = getProvider();
                const shopContract = getShopContract(provider);
                const signer = provider.getSigner(currentAccountAddress);
                if (ids.length > 1) {
                    const tx = await shopContract
                        .connect(signer)
                        .burnBatchFromShop(ids, amounts);
                    await tx.wait(); // ждем ее завершения
                }
                if (ids.length === 1) {
                    const tx = await shopContract
                        .connect(signer)
                        .burnFromShop(ids[0], amounts[0]);
                    await tx.wait(); // ждем ее завершения
                }
                await getShopTokensBalance();
            }
        } catch (e) {
            errorHandler(e);
        }
    }


    //========= WITHDRAW ALL =========//
    const onWithdrawHandler = async () => {
        try {
            if (currentAccountAddress) {
                setLocalLoading(true);
                const provider = getProvider();
                const shopContract = getShopContract(provider);
                const signer = provider.getSigner(currentAccountAddress);
                const tx = await shopContract.connect(signer).withdrawAll();
                await tx.wait();
                await getShopBalance();
            }
        } catch (e: any) {
            errorHandler(e);
        } finally {
            setLocalLoading(false);
        }
    }

    return (
        <div className={style.admin}>

            {
                loading &&
                <LinearProgress className={style.progress}
                                color="secondary"
                />
            }

            <Typography variant="h4" color="primary" align="center">
                Admin
            </Typography>

            <Typography variant="h5" color="primary" align="center">
                Manage your shop
            </Typography>

            <div className={style.borderBlock}>
                <div className={style.infoBlockColumn}>
                    <Typography className={style.label}>Shop address</Typography>
                    <Typography className={style.value}>{shopAddress}</Typography>
                </div>

                {
                    shopOwner &&
                    <div className={style.infoBlockColumn}>
                        <Typography className={style.label}>Shop owner</Typography>
                        <Typography className={style.value}>{shopOwner}</Typography>
                    </div>
                }

                {
                    shopBalance &&
                    <div className={style.infoBlockColumn}>
                        <Typography className={style.label}>Shop balance (wei)</Typography>
                        <Typography className={style.value}>{shopBalance}</Typography>
                    </div>
                }

                {
                    shopTokensBalance && (
                        <Balances balances={shopTokensBalance}/>
                    )
                }


            </div>

            {
                pricesBuy && (
                    <div className={style.borderBlock}>
                        <Typography variant="h6" align="center" className={style.title}>
                            Change price for buy tokens from shop
                        </Typography>
                        {
                            pricesBuy.map((price, key) => (
                                <PriceForm key={key}
                                           price={price}
                                           img={items[key].img_small}
                                           label={items[key].label}
                                           handler={onChangePriceBuy(key)}
                                />
                            ))
                        }
                    </div>
                )
            }

            {
                pricesSell && (
                    <div className={style.borderBlock}>
                        <Typography variant="h6" align="center" className={style.title}>
                            Change price for sell tokens to shop
                        </Typography>
                        {
                            pricesSell.map((price, key) => (
                                <PriceForm key={key}
                                           price={price}
                                           img={items[key].img_small}
                                           label={items[key].label}
                                           handler={onChangePriceSell(key)}
                                />
                            ))
                        }
                    </div>
                )
            }

            <BatchForm title="Mint"
                       btnLabel="mint"
                       handler={mint}
            />

            <BatchForm title="Burn"
                       btnLabel="Burn"
                       handler={burn}
            />


            <Button variant="contained"
                    fullWidth
                    className={style.btn}
                    size="small"
                    color="warning"
                    onClick={onWithdrawHandler}
                    disabled={!window.ethereum || localLoading || Number(shopBalance) === 0}
            >
                <div className={style.preloaderWrapper}>
                    <p>Withdraw all</p>
                    {
                        localLoading &&
                        <div className={style.preloader}>
                            <CircularProgress color="success" size={25}/>
                        </div>
                    }
                </div>
            </Button>
        </div>
    )
})
