import React, {FC, useState} from "react";
import style from "./Transfer.module.scss"
import {FormikErrors, FormikHelpers, useFormik} from "formik";
import {TextField, Typography} from "@mui/material";
import Button from "@mui/material/Button";
import Checkbox from "@mui/material/Checkbox";
import {items} from "../../../assets/jpeg/items";
import CircularProgress from "@mui/material/CircularProgress";
import {ethers} from "ethers";

interface IValues {
    to: string
    silver: number
    gold: number
    sword: number
    shield: number
    hammer: number
}

interface ITransfer {
    handler: (to: string, ids: number[], amounts: number[]) => Promise<void>
}

export const Transfer: FC<ITransfer> = ({
                                              handler,
}) => {

    const [loading, setLoading] = useState(false);

    const initialValues: IValues = {
        to: "",
        silver: 1,
        gold: 0,
        sword: 0,
        shield: 0,
        hammer: 0,

    }
    const validate = ({to, ...items}: IValues): FormikErrors<IValues> => {
        const errors = {} as FormikErrors<IValues>
        if (!to) {
            errors.to = "required"
        }
        if (to && !ethers.utils.isAddress(to)) {
            errors.to = "address is not valid"
        }
        return errors
    }

    const onSubmit = async (values: IValues, formikHelpers: FormikHelpers<IValues>) => {
        try {
            setLoading(true);
            console.log(values)
            const {to, ...items} = values;
            const ids = [] as number[];
            const amounts = [] as number[];
            for (let i = 0; i < Object.keys(items).length; i++) {
                if (Object.values(items)[i]) {
                    ids.push(i);
                    amounts.push(Object.values(items)[i])
                }
            }
            await handler(to, ids, amounts);
        } catch (e: any) {
            //
        } finally {
            formikHelpers.resetForm();
            setChecked({
                silver: true,
                gold: false,
                sword: false,
                shield: false,
                hammer: false,
            })
            setLoading(false);
        }
    }
    const formik = useFormik({
        initialValues,
        onSubmit,
        validate: validate,
    });

    const [checked, setChecked] = useState({
        silver: true,
        gold: false,
        sword: false,
        shield: false,
        hammer: false,
    });

    return (
        <div className={style.transfer}>

            <Typography variant="h6" align="center" className={style.title}>
                Transfer tokens to account
            </Typography>

            <form onSubmit={formik.handleSubmit}
                  className={style.form}
            >
                <TextField fullWidth
                           size="small"
                           label="to"
                           {...formik.getFieldProps("to")}
                           disabled={!window.ethereum || loading}
                           className={style.field}
                           sx={{
                               "& .MuiInputBase-input": {
                                   fontSize: "24px",
                               },
                               "& .MuiFormHelperText-root": {
                                   position: "absolute",
                                   left: 0,
                                   bottom: "-20px",
                               }
                           }}
                           error={formik.touched.to && Boolean(formik.errors.to)}
                           helperText={formik.touched.to && formik.errors.to}
                />

                <div className={style.items}>
                    {
                        [
                            { field: "silver" },
                            { field: "gold" },
                            { field: "sword" },
                            { field: "shield" },
                            { field: "hammer" },
                        ].map(({field}, key) => (
                            <div className={style.row} key={key}>
                                <Checkbox
                                    // @ts-ignore
                                    checked={checked[field]}
                                    onChange={(e) => {
                                        setChecked({...checked, [field]: e.target.checked})
                                        if (!e.target.checked) {
                                            formik.setFieldValue(field, 0);
                                        } else {
                                            formik.setFieldValue(field, 1);
                                        }
                                    }}
                                    disableRipple
                                    disableFocusRipple
                                    disableTouchRipple
                                    inputProps={{'aria-label': 'controlled'}}
                                    sx={{
                                        padding: 0,
                                        "& .MuiSvgIcon-root": {
                                            width: 44,
                                            height: 44
                                        }
                                    }}
                                />
                                <TextField fullWidth
                                           size="small"
                                           type="number"
                                           label={items[key].label}
                                           inputProps={{
                                               min: 0
                                           }}
                                           {...formik.getFieldProps(field)}
                                    // @ts-ignore
                                           disabled={!checked[field] || !window.ethereum || loading}
                                           className={style.field}
                                           sx={{
                                               "& .MuiInputBase-input": {
                                                   fontSize: "28px",
                                               },
                                           }}
                                />
                                <img src={items[key].img_small} alt="" className={style.icon}/>
                            </div>
                        ))
                    }
                </div>

                <Button type="submit"
                        variant="contained"
                        fullWidth
                        className={style.btn}
                        disabled={
                            !window.ethereum ||
                            loading ||
                            (!checked.silver && !checked.gold && !checked.sword && !checked.shield && !checked.hammer)
                        }
                        size="small"
                        color="secondary"
                        disableElevation
                >
                    <div className={style.preloaderWrapper}>
                        <p>transfer</p>
                        {
                           loading && (
                               <div className={style.preloader}>
                                   <CircularProgress size={25}/>
                               </div>
                           )
                        }
                    </div>
                </Button>

            </form>
        </div>
    )
}

