import {
    BuyBatchLogUnhandledType,
    BuyLogUnhandledType, MintBatchLogUnhandledType, MintLogUnhandledType,
    PriceLogUnhandledType, SellBatchLogUnhandledType,
    SellLogUnhandledType
} from "../types/logs.type";
import {getDate} from "./helpers";

export const priceLogHandler = (args: PriceLogUnhandledType) => (
    args ? ({
        tokenId: String(args[0].toNumber()),
        oldValue: String(args[1].toNumber()),
        newValue: String(args[2].toNumber()),
        timestamp: getDate(args[3].toNumber()),
    }) : ({
        tokenId: '',
        oldValue: '',
        newValue: '',
        timestamp: '',
    })
);

export const buyLogHandler = (args: BuyLogUnhandledType) => (
    args ? ({
        buyer: args[0],
        tokenId: String(args[1].toNumber()),
        amount: String(args[2].toNumber()),
        price: String(args[3].toNumber()),
        timestamp: getDate(args[4].toNumber()),
    }) : ({
        buyer: '',
        tokenId: '',
        amount: '',
        price: '',
        timestamp: '',
    })
);

export const buyBatchLogHandler = (args: BuyBatchLogUnhandledType) => (
    args ? ({
        buyer: args[0],
        ids: args[1].map(el => el.toNumber()).toString(),
        amounts: args[2].map(el => el.toNumber()).toString(),
        prices: args[3].map(el => el.toNumber()).toString(),
        timestamp: getDate(args[4].toNumber()),
    }) : ({
        buyer: '',
        ids: '',
        amounts: '',
        prices: '',
        timestamp: '',
    })
);

