import React, {FC} from "react";
import style from "./Balances.module.scss";
import {items} from "../../../assets/jpeg/items";
import {numberFormat} from "../../../helpers/helpers";

interface IBalances {
    balances: number[]
    pricesBuy?: number[]
    pricesSell?: number[]

}

export const Balances: FC<IBalances> = ({
                                            balances,
                                            pricesBuy,
                                            pricesSell,

                                        }) => {
    return (
        <div className={style.balances}>
            <div className={style.items}>
                {
                    balances.map((balance, index) => (
                        <div className={style.item} key={index}>
                            <div className={style.imgWrapper}>
                                <img src={items[index].img_small} alt=""/>
                            </div>
                            <p className={style.label}>{items[index].label}</p>
                            <p className={style.price}>
                                Balance: <span>{numberFormat(balance)}</span>
                            </p>
                            {
                                pricesBuy && (
                                    <p className={style.price}>
                                        Price for buy: <span>{pricesBuy[index]}</span> wei
                                    </p>
                                )
                            }
                            {
                                pricesSell && (
                                    <p className={style.price}>
                                        Price for sell: <span>{pricesSell[index]}</span> wei
                                    </p>
                                )

                            }
                        </div>
                    ))
                }
            </div>

        </div>
    )
}
