import React, {FC, useState} from "react";
import style from "./PriceForm.module.scss";
import {FormikHelpers, useFormik} from "formik";
import {TextField} from "@mui/material";
import CircularProgress from "@mui/material/CircularProgress";
import Button from "@mui/material/Button";
import SendIcon from '@mui/icons-material/Send';

interface IValues {
    price: number
}

interface IPriceForm {
    label: string
    price: number
    img: string
    handler: (price: number) => Promise<void>
}

export const PriceForm: FC<IPriceForm> = ({
                                              label,
                                              price,
                                              img,
                                              handler,
                                          }) => {
    const [loading, setLoading] = useState(false);
    const initialValues: IValues = {
        price,
    }
    const onSubmit = async ({price}: IValues, formikHelpers: FormikHelpers<IValues>) => {
        try {
            setLoading(true);
            await handler(price);
        } catch (e: any) {
            //
        } finally {
            setLoading(false);
        }
    }
    const formik = useFormik({
        initialValues,
        onSubmit,
        enableReinitialize: true,
    });


    return (
        <form onSubmit={formik.handleSubmit}
              className={style.priceForm}
        >

            <img src={img} alt="" className={style.icon}/>

            <TextField fullWidth
                       size="small"
                       type="number"
                       label={label}
                       inputProps={{
                           min: 0
                       }}
                       {...formik.getFieldProps("price")}
                        // @ts-ignore
                       disabled={!window.ethereum || loading}
                       className={style.field}
                       sx={{
                           "& .MuiInputBase-input": {
                               fontSize: "28px",
                           },
                       }}
            />

            <Button type="submit"
                    variant="contained"
                    className={style.btn}
                    disabled={!window.ethereum || loading}
                    size="small"
                    color="secondary"
                    disableElevation
            >
                    {
                        loading ? <CircularProgress size={25}/> : <SendIcon/>
                    }
            </Button>

        </form>
    )
}
