import React, {useEffect} from "react";
import style from "./Shop.module.scss";
import {observer} from "mobx-react-lite";
import {chainId, getProvider, getShopContract, getTokenContract, shopAddress} from "../../helpers/ethers.helper";
import {useStore} from "../../store/useStore";
import {LinearProgress, Typography} from "@mui/material";
import {Balances} from "./Balances/Balances";
import {BatchForm} from "../X_common/BatchForm/BatchForm";
import {Transfer} from "./Transfer/Transfer";
import {ethers} from "ethers";

export const Shop = observer(() => {
    const {
        cryptoStore: {
            currentAccountAddress, loading, errorHandler,
            pricesBuy, pricesSell,
            getPricesBuy, getPricesSell,
            currentAccountTokenBalance, getCurrentAccountTokenBalance,
            balance, getCurrentAccountBalance,
            shopTokensBalance, getShopTokensBalance,
        }
    } = useStore();

    //========= МОНТИРОВАНИЕ =========//
    useEffect(() => {
        const omMountHandler = async () => {
            if (window.ethereum) {
                const provider = getProvider();
                const network = await provider.getNetwork();

                // проверка совпадения сети в которой развернут смарт-контракт с той, к которой подключились
                if (network.chainId === chainId) {
                    await getPricesBuy();
                    await getPricesSell();
                    await getShopTokensBalance();
                    await getCurrentAccountTokenBalance();
                    currentAccountAddress && await getCurrentAccountBalance(currentAccountAddress);
                }
            }
        }
        omMountHandler().then();
    }, [window.ethereum, currentAccountAddress]);

    // BUY
    const onBuyHandler = async (ids: number[], amounts: number[]) => {
        try {
            if (window.ethereum && currentAccountAddress && pricesBuy) {
                const provider = getProvider();
                const shopContract = getShopContract(provider);
                const signer = provider.getSigner(currentAccountAddress);
                const value = ids.reduce((acc, id, index) => acc + pricesBuy[id] * amounts[index], 0);

                if (ids.length > 1) {
                    const tx = await shopContract
                        .connect(signer)
                        .buyBatch(ids, amounts, {value});
                    await tx.wait(); // ждем ее завершения
                }

                if (ids.length === 1) {
                    const tx = await shopContract
                        .connect(signer)
                        .buy(ids[0], amounts[0], {value});
                    await tx.wait(); // ждем ее завершения
                }

                await getCurrentAccountTokenBalance();
                await getCurrentAccountBalance(currentAccountAddress);
                await getShopTokensBalance();

            }
        } catch (e) {
            errorHandler(e);
        }
    }

    // SELL
    const onSellHandler = async (ids: number[], amounts: number[]) => {
        try {
            if (window.ethereum && currentAccountAddress) {
                const provider = getProvider();
                const shopContract = getShopContract(provider);
                const tokenContract = getTokenContract(provider);
                const signer = provider.getSigner(currentAccountAddress);


                // set approval for shop
                const addApprovalTx = await tokenContract.connect(signer).setApprovalForAll(shopAddress, true);
                await addApprovalTx.wait();

                if (ids.length > 1) {
                    const tx = await shopContract
                        .connect(signer)
                        .sellBatch(ids, amounts);
                    await tx.wait(); // ждем ее завершения
                }

                if (ids.length === 1) {
                    const tx = await shopContract
                        .connect(signer)
                        .sell(ids[0], amounts[0]);
                    await tx.wait(); // ждем ее завершения
                }

                // set approval for shop
                const removeApprovalTx = await tokenContract.connect(signer).setApprovalForAll(shopAddress, false);
                await removeApprovalTx.wait();

                await getCurrentAccountTokenBalance();
                await getCurrentAccountBalance(currentAccountAddress);
                await getShopTokensBalance();
            }
        } catch (e) {
            errorHandler(e);
        }
    }

    // TRANSFER
    const onTransferHandler = async (to: string, ids: number[], amounts: number[]) => {
        try {
            if (window.ethereum && currentAccountAddress) {
                console.log(ids, amounts);
                const provider = getProvider();
                const tokenContract = getTokenContract(provider);
                const signer = provider.getSigner(currentAccountAddress);
                if (ids.length > 1) {
                    const tx = await tokenContract
                        .connect(signer)
                        .safeBatchTransferFrom(currentAccountAddress, to, ids, amounts, ethers.constants.HashZero);
                    await tx.wait(); // ждем ее завершения
                }

                if (ids.length === 1) {
                    const tx = await tokenContract
                        .connect(signer)
                        .safeTransferFrom(currentAccountAddress, to, ids[0], amounts[0], ethers.constants.HashZero);
                    await tx.wait(); // ждем ее завершения
                }

                await getCurrentAccountTokenBalance();
            }
        } catch (e) {
            errorHandler(e);
        }
    }


    return (
        <div className={style.shop}>

            {
                loading &&
                <LinearProgress className={style.progress}
                                color="secondary"
                />
            }

            <Typography variant="h4" color="primary" align="center">
                Shop
            </Typography>

            <Typography variant="h5" color="primary" align="center">
                Buy, sell or transfer ERC1155 tokens
            </Typography>

            <div className={style.borderBlock}>
                <Typography variant="h6" align="center" className={style.blockTitle}>
                    Shop
                </Typography>

                {
                    pricesBuy && pricesSell && shopTokensBalance && (
                        <Balances pricesBuy={pricesBuy}
                                  pricesSell={pricesSell}
                                  balances={shopTokensBalance}
                        />
                    )
                }

            </div>

            {
                currentAccountAddress && currentAccountTokenBalance && typeof balance === "string" && (
                    <div className={style.borderBlock}>
                        <Typography variant="h6" align="center" className={style.blockTitle}>
                            Account
                        </Typography>

                        <div className={style.infoBlockColumn}>
                            <Typography className={style.label}>Current account ether balance</Typography>
                            <Typography className={style.value}>
                                {balance} <span>x10</span><span className={style.sup}>-18</span>
                            </Typography>
                        </div>

                        <Balances balances={currentAccountTokenBalance}/>

                        <BatchForm title="Buy tokens from shop"
                                   btnLabel="buy"
                                   handler={onBuyHandler}
                        />

                        <BatchForm title="Sell tokens to shop"
                                   btnLabel="sell"
                                   handler={onSellHandler}
                        />

                        <Transfer handler={onTransferHandler}/>
                    </div>
                )

            }


        </div>
    )
})
