import React, {FC, useState} from "react";
import style from "./BatchForm.module.scss"
import {FormikHelpers, useFormik} from "formik";
import {TextField, Typography} from "@mui/material";
import Button from "@mui/material/Button";
import Checkbox from "@mui/material/Checkbox";
import {items} from "../../../assets/jpeg/items";
import CircularProgress from "@mui/material/CircularProgress";

interface IValues {
    silver: number
    gold: number
    sword: number
    shield: number
    hammer: number
}

interface IBatchForm {
    title: string
    btnLabel: string
    handler: (ids: number[], amounts: number[]) => Promise<void>
}

export const BatchForm: FC<IBatchForm> = ({
                                              title,
                                              btnLabel,
                                              handler,
}) => {

    const [loading, setLoading] = useState(false);

    const initialValues: IValues = {
        silver: 1,
        gold: 0,
        sword: 0,
        shield: 0,
        hammer: 0,
    }
    const onSubmit = async (values: IValues, formikHelpers: FormikHelpers<IValues>) => {
        try {
            setLoading(true);
            const ids = [] as number[];
            const amounts = [] as number[];
            for (let i = 0; i < Object.keys(values).length; i++) {
                if (Object.values(values)[i]) {
                    ids.push(i);
                    amounts.push(Object.values(values)[i])
                }
            }
            await handler(ids, amounts);
        } catch (e: any) {
            //
        } finally {
            formikHelpers.resetForm();
            setChecked({
                silver: true,
                gold: false,
                sword: false,
                shield: false,
                hammer: false,
            })
            setLoading(false);
        }
    }
    const formik = useFormik({
        initialValues,
        onSubmit,
    });

    const [checked, setChecked] = useState({
        silver: true,
        gold: false,
        sword: false,
        shield: false,
        hammer: false,
    });

    return (
        <div className={style.batchForm}>

            <Typography variant="h6" align="center" className={style.title}>
                {title}
            </Typography>

            <form onSubmit={formik.handleSubmit}
                  className={style.form}
            >
                {
                    [
                        { field: "silver" },
                        { field: "gold" },
                        { field: "sword" },
                        { field: "shield" },
                        { field: "hammer" },
                    ].map(({field}, key) => (
                        <div className={style.row} key={key}>
                            <Checkbox
                                // @ts-ignore
                                checked={checked[field]}
                                onChange={(e) => {
                                    setChecked({...checked, [field]: e.target.checked})
                                    if (!e.target.checked) {
                                        formik.setFieldValue(field, 0);
                                    } else {
                                        formik.setFieldValue(field, 1);
                                    }
                                }}
                                disableRipple
                                disableFocusRipple
                                disableTouchRipple
                                inputProps={{'aria-label': 'controlled'}}
                                sx={{
                                    padding: 0,
                                    "& .MuiSvgIcon-root": {
                                        width: 44,
                                        height: 44
                                    }
                                }}
                            />
                            <TextField fullWidth
                                       size="small"
                                       type="number"
                                       label={items[key].label}
                                       inputProps={{
                                           min: 0
                                       }}
                                       {...formik.getFieldProps(field)}
                                       // @ts-ignore
                                       disabled={!checked[field] || !window.ethereum || loading}
                                       className={style.field}
                                       sx={{
                                           "& .MuiInputBase-input": {
                                               fontSize: "28px",
                                           },
                                       }}
                            />
                            <img src={items[key].img_small} alt="" className={style.icon}/>
                        </div>
                    ))
                }

                <Button type="submit"
                        variant="contained"
                        fullWidth
                        className={style.btn}
                        disabled={
                            !window.ethereum ||
                            loading ||
                            (!checked.silver && !checked.gold && !checked.sword && !checked.shield && !checked.hammer)
                        }
                        size="small"
                        color="secondary"
                        disableElevation
                >
                    <div className={style.preloaderWrapper}>
                        <p>{btnLabel}</p>
                        {
                           loading && (
                               <div className={style.preloader}>
                                   <CircularProgress size={25}/>
                               </div>
                           )
                        }
                    </div>
                </Button>

            </form>
        </div>
    )
}

