import React, {useEffect, useState} from "react";
import style from "./Logs.module.scss";
import {Typography} from "@mui/material";
import {observer} from "mobx-react-lite";
import {useStore} from "../../store/useStore";
// import {
//     BuySellShopLogUnhandledType,
//     IBuyFromShopLog,
//     IMintLog,
//     IPriceLog,
//     ISellToShopLog, MintLogUnhandledType,
//     PriceLogUnhandledType
// } from "../../types/logs.type";
import {chainId, getProvider, getShopContract} from "../../helpers/ethers.helper";
// import {
//     buyFromShopLogArgsHandler,
//     mintLogArgsHandler,
//     priceLogArgsHandler,
//     sellToShopLogArgsHandler
// } from "../../helpers/logs.helpers";
import {LogsTable} from "./LogsTable/LogsTable";
import {
    BuyBatchLogUnhandledType,
    BuyLogUnhandledType,
    IBuyBatchLog,
    IBuyLog, IMintBatchLog, IMintLog,
    IPriceLog, ISellBatchLog,
    ISellLog, MintBatchLogUnhandledType, MintLogUnhandledType,
    PriceLogUnhandledType,
    SellBatchLogUnhandledType,
    SellLogUnhandledType
} from "../../types/logs.type";
import {
    buyBatchLogHandler,
    buyLogHandler, mintBatchLogHandler, mintLogHandler,
    priceLogHandler,
    sellBatchLogHandler,
    sellLogHandler
} from "../../helpers/logs.helpers";

export const Logs = observer(() => {
    const {cryptoStore: {errorHandler}} = useStore();

    const [priceBuyLogs, setPriceBuyLogs] = useState<IPriceLog[]>([]);
    const [priceSellLogs, setPriceSellLogs] = useState<IPriceLog[]>([]);
    const [buyLogs, setBuyLogs] = useState<IBuyLog[]>([]);
    const [sellLogs, setSellLogs] = useState<ISellLog[]>([]);
    const [buyBatchLogs, setBuyBatchLogs] = useState<IBuyBatchLog[]>([]);
    const [sellBatchLogs, setSellBatchLogs] = useState<ISellBatchLog[]>([]);
    const [mintLogs, setMintLogs] = useState<IMintLog[]>([]);
    const [burnLogs, setBurnLogs] = useState<IMintLog[]>([]);
    const [mintBatchLogs, setMintBatchLogs] = useState<IMintBatchLog[]>([]);
    const [burnBatchLogs, setBurnBatchLogs] = useState<IMintBatchLog[]>([]);


    //========= GET PRICE BUY LOGS =========//
    const getPriceBuyLogs = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getShopContract(provider);
                const filter = contract.filters.PriceBuy();
                const logs = await contract.queryFilter(filter);
                setPriceBuyLogs(logs.map(({args}: { args: PriceLogUnhandledType }) => priceLogHandler(args)))
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= GET PRICE SELL LOGS =========//
    const getPriceSellLogs = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getShopContract(provider);
                const filter = contract.filters.PriceSell();
                const logs = await contract.queryFilter(filter);
                setPriceSellLogs(logs.map(({args}: { args: PriceLogUnhandledType }) => priceLogHandler(args)))
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= GET BUY LOGS =========//
    const getBuyLogs = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getShopContract(provider);
                const filter = contract.filters.Buy();
                const logs = await contract.queryFilter(filter);
                setBuyLogs(logs.map(({args}: { args: BuyLogUnhandledType }) => buyLogHandler(args)))
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= GET SELL LOGS =========//
    const getSellLogs = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getShopContract(provider);
                const filter = contract.filters.Sell();
                const logs = await contract.queryFilter(filter);
                setSellLogs(logs.map(({args}: { args: SellLogUnhandledType }) => sellLogHandler(args)))
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= GET BUY BATCH LOGS =========//
    const getBuyBatchLogs = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getShopContract(provider);
                const filter = contract.filters.BuyBatch();
                const logs = await contract.queryFilter(filter);
                setBuyBatchLogs(logs.map(({args}: { args: BuyBatchLogUnhandledType }) => buyBatchLogHandler(args)))
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= GET SELL BATCH LOGS =========//
    const getSellBatchLogs = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getShopContract(provider);
                const filter = contract.filters.SellBatch();
                const logs = await contract.queryFilter(filter);
                setSellBatchLogs(logs.map(({args}: { args: SellBatchLogUnhandledType }) => sellBatchLogHandler(args)))
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= GET MINT LOGS =========//
    const getMintLogs = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getShopContract(provider);
                const filter = contract.filters.Mint();
                const logs = await contract.queryFilter(filter);
                setMintLogs(logs.map(({args}: { args: MintLogUnhandledType }) => mintLogHandler(args)))
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= GET BURN LOGS =========//
    const getBurnLogs = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getShopContract(provider);
                const filter = contract.filters.Burn();
                const logs = await contract.queryFilter(filter);
                setBurnLogs(logs.map(({args}: { args: MintLogUnhandledType }) => mintLogHandler(args)))
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= GET MINT BATCH LOGS =========//
    const getMintBatchLogs = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getShopContract(provider);
                const filter = contract.filters.MintBatch();
                const logs = await contract.queryFilter(filter);
                setMintBatchLogs(logs.map(({args}: { args: MintBatchLogUnhandledType }) => mintBatchLogHandler(args)))
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= GET BURN BATCH LOGS =========//
    const getBurnBatchLogs = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getShopContract(provider);
                const filter = contract.filters.BurnBatch();
                const logs = await contract.queryFilter(filter);
                setBurnBatchLogs(logs.map(({args}: { args: MintBatchLogUnhandledType }) => mintBatchLogHandler(args)))
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= ADD EVENT LISTENERS =========
    const addListener = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const shopContract = getShopContract(provider);
                const startBlockNumber = await provider.getBlockNumber();

                provider.on("error", (e) => console.log(e));

                // @ts-ignore
                shopContract.on("PriceBuy", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getPriceBuyLogs();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                })

                // @ts-ignore
                shopContract.on("PriceSell", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getPriceSellLogs();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                })

                // @ts-ignore
                shopContract.on("Buy", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getBuyLogs();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                })

                // @ts-ignore
                shopContract.on("Sell", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getSellLogs();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                })

                // @ts-ignore
                shopContract.on("BuyBatch", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getBuyBatchLogs();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                })

                // @ts-ignore
                shopContract.on("SellBatch", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getSellBatchLogs();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                })

                // @ts-ignore
                shopContract.on("Mint", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getMintLogs();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                })

                // @ts-ignore
                shopContract.on("Burn", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getBurnLogs();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                })

                // @ts-ignore
                shopContract.on("BurnBatch", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getBurnBatchLogs();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                })

                // @ts-ignore
                shopContract.on("MintBatch", async (...args) => {
                    try {
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getMintBatchLogs();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                })

            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= МОНТИРОВАНИЕ =========//
    useEffect(() => {
        const omMountHandler = async () => {
            if (window.ethereum) {
                const provider = getProvider();
                const network = await provider.getNetwork();

                // проверка совпадения сети в которой развернут смарт-контракт с той, к которой подключились
                if (network.chainId === chainId) {
                    await addListener();
                    await getPriceBuyLogs();
                    await getPriceSellLogs();
                    await getBuyLogs();
                    await getSellLogs();
                    await getBuyBatchLogs();
                    await getSellBatchLogs();
                    await getBurnLogs();
                    await getMintLogs();
                    await getBurnBatchLogs();
                    await getMintBatchLogs();
                }
            }
        }
        omMountHandler().then();
    }, [window.ethereum]);

    return (
        <div className={style.logs}>

            <Typography variant="h4" color="primary">
                Logs
            </Typography>

            <LogsTable tableLibel="Buy From Shop Price Change"
                       headerLabels={["tokenId", "oldValue", "newValue", "timestamp"]}
                       // @ts-ignore
                       logs={priceBuyLogs}
                       headerClassName="header_price"
                       rowClassName="row_price"
            />

            <LogsTable tableLibel="Sell To Shop Price Change"
                       headerLabels={["tokenId", "oldValue", "newValue", "timestamp"]}
                       // @ts-ignore
                       logs={priceSellLogs}
                       headerClassName="header_price"
                       rowClassName="row_price"
            />

            <LogsTable tableLibel="Buy From Shop"
                       headerLabels={["buyer", "tokenId", "amount", "price", "timestamp"]}
                        // @ts-ignore
                       logs={buyLogs}
                       headerClassName="header_buy_sell"
                       rowClassName="row_buy_sell"
            />

            <LogsTable tableLibel="Buy Batch From Shop"
                       headerLabels={["buyer", "ids", "amounts", "prices", "timestamp"]}
                        // @ts-ignore
                       logs={buyBatchLogs}
                       headerClassName="header_buy_sell"
                       rowClassName="row_buy_sell"
            />

            <LogsTable tableLibel="Sell To Shop"
                       headerLabels={["seller", "tokenId", "amount", "price", "timestamp"]}
                        // @ts-ignore
                       logs={sellLogs}
                       headerClassName="header_buy_sell"
                       rowClassName="row_buy_sell"
            />

            <LogsTable tableLibel="Sell Batch To Shop"
                       headerLabels={["seller", "ids", "amounts", "prices", "timestamp"]}
                        // @ts-ignore
                       logs={sellBatchLogs}
                       headerClassName="header_buy_sell"
                       rowClassName="row_buy_sell"
            />

            <LogsTable tableLibel="Mint"
                       headerLabels={["tokenId", "amount", "timestamp"]}
                        // @ts-ignore
                       logs={mintLogs}
                       headerClassName="header_mint"
                       rowClassName="row_mint"
            />

            <LogsTable tableLibel="MintBatch"
                       headerLabels={["ids", "amounts", "timestamp"]}
                // @ts-ignore
                       logs={mintBatchLogs}
                       headerClassName="header_mint"
                       rowClassName="row_mint"
            />

            <LogsTable tableLibel="Burn"
                       headerLabels={["tokenId", "amount", "timestamp"]}
                        // @ts-ignore
                       logs={burnLogs}
                       headerClassName="header_mint"
                       rowClassName="row_mint"
            />

            <LogsTable tableLibel="BurnBatch"
                       headerLabels={["ids", "amounts", "timestamp"]}
                        // @ts-ignore
                       logs={burnBatchLogs}
                       headerClassName="header_mint"
                       rowClassName="row_mint"
            />

        </div>
    )
})
